import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconAlignLeft, IconAlignCenter, IconAlignJustify, IconAlignRight } from '../icons';

export function AlignLeftButton(props: IIconButtonProps) {
  const {
    icon = <IconAlignLeft />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="align_left"
      icon={icon}
      {...rest}
    />
  )
}

